import React from 'react'


class Status extends React.Component{
     
  constructor(props){
    
    
    super(props);
           
    this.state={
       index:0,
       quoteStatus:[]
    }; 
    
    this.handleLikeClick=this.handleLikeClick.bind(this);
    this.handleDislikeClick=this.handleDislikeClick.bind(this);
  }
  
  handleLikeClick(){
    fetch(this.props.data).then(res=>{      
     
        this.setState(state=>({
         index:state.index,
         quoteStatus:state.quoteStatus.slice(0,this.props.data.index).concat({likes:state.quoteStatus[state.index].likes+1,dislikes:state.quoteStatus[state.index].dislikes}).concat(state.quoteStatus.slice(this.props.data.index+1))
        }));
      
        localStorage.setItem("Data",JSON.stringify(this.state.quoteStatus)); 
    })
  }
  
  handleDislikeClick(){
    
    fetch(this.props.data).then(res=>{ 
         
         this.setState(state=>({
         index:state.index,
         
         quoteStatus:state.quoteStatus.slice(0,this.props.data.index).concat({likes:state.quoteStatus[state.index].likes,dislikes:state.quoteStatus[state.index].dislikes+1}).concat(state.quoteStatus.slice(this.props.data.index+1))
        }));
      
      localStorage.setItem("Data",JSON.stringify(this.state.quoteStatus));
    });
  }
 

 async componentDidUpdate(){
     let arr=[];
  
         //let data= await this.props.data;
        if(localStorage.getItem("Data")===null){
          console.log(localStorage.getItem("Data"));
         for(let i=0;i<this.props.data.quotesArr.length;i++){
           arr.push({
             likes:0,
             dislikes:0
           });
           
         }
         
          
           localStorage.setItem("Data",JSON.stringify(arr));
        }
        if(this.state.index!==this.props.data.index){
          this.setState({
           index: this.props.data.index,
           quoteStatus:JSON.parse(localStorage.getItem("Data")|| "[]")
         });

    }
     
      console.log("index "+this.props.data.index)
       //console.log(localStorage.getItem("Data")) 
       // console.log(arr)
     
    
 //});
 
  }
  
  render(){ 
    let val1=0;
    let val2=0;
     if(this.state.quoteStatus.length<1)
      return null;


       val1=this.state.quoteStatus[this.state.index].likes; 
       val2=this.state.quoteStatus[this.state.index].dislikes;

       return(
     
        <div id="status">  
          
            
             <button id="like" onClick={this.handleLikeClick}><i className="fa fa-thumbs-o-up"></i> {val1} </button>
             <button id="dislike" onClick={this.handleDislikeClick}><i className="fa fa-thumbs-o-down" ></i> {val2}</button>
             
          
          
        </div>
     

     
    
  
     
     
    );
  }
}

 
export default Status 