import React from 'react'
import Status from './Status';

const quoteAPI='https://gist.githubusercontent.com/camperbot/5a022b72e96c4c9585c32bf6a75f62d9/raw/e3c6895ce42069f0ee7e991229064f167fe8ccdc/quotes.json'

  class QuoteBox extends React.Component{
    constructor(props){
      super(props);
      this.state={
        quotesArr:[],
        quote: 'hello' ,
        auth: ''
      }
      this.handleClick=this.handleClick.bind(this);
     
    }
    
    handleClick(){
       let x=Math.floor(Math.random()*this.state.quotesArr.length);
      this.setState(state=>(
       
      {
        index:x,
        quotesArr:state.quotesArr, 
        quote:state.quotesArr[x].quote,
        auth:state.quotesArr[x].author
      }));
    }
   
    
    componentDidMount(){
      
      fetch(quoteAPI).then(res=>res.json()).then(res=>{ 
       let newIndex=Math.floor(Math.random()*res.quotes.length);
        
       this.setState({
        quotesArr: res.quotes,
        quote: res.quotes[newIndex].quote,
        auth: res.quotes[newIndex].author,
        index: newIndex
      })
       
    });
   }
  
    
   
    render(){
      let tweetUrl="https://twitter.com/intent/tweet?text="+encodeURIComponent(this.state.quote+" By "+this.state.auth);
      
      return ( 
        
        <div className="wrapper d-flex justify-content-center align-items-center">
          <div id="quoteContainer" className="col-9">
            <div id="quote-text">
              <p id="text"><i className="fa fa-quote-left"></i> {this.state.quote} <i className="fa fa-quote-right"></i></p>
              <div id="author-cont" className="d-flex justify-content-end">
                <p id="author">-{this.state.auth}</p> 
              </div>
            </div>
            
              <div className="button-div d-flex justify-content-between">
                
               
                 <a id="tweet-quote" className=" bg-primary text-primary buttons d-flex align-items-center justify-content-center" target="_top" href={tweetUrl}><i className="t-icon fa fa-twitter"></i></a>
                                
                   <button className=" d-flex align-items-center justify-content-center buttons btn btn-primary" id="new-quote" onClick={this.handleClick}>New Quote</button>
                
            </div> 
            <Status data={this.state} />
            
            </div>
          
       </div>
       
      ); 
        
    } 
  }

  export default QuoteBox